const API = () => {
    return {
        recently : `${URL()}/recently`, //    tx  10
        miners : `${URL()}/miners`, //   

        recentblocks : `${URL()}/recentblocks`, //   

        blocks : `${URL()}/blocks`, // { tx,    }, path > /:page  , 25  ,
        block : `${URL()}/block`, // path > /:number  ,    

        transactions : `${URL()}/transactions`, // path > /:page  , 25  
        transaction : `${URL()}/transaction`, // path > /:hash  , tx   

        account : `${URL()}/account`, // path > /:account  ,   
        accountTransaction :  `${URL()}/transactions/account`, // path > /:account { tx,   }, /:account/:page { tx 25   }
    }
};

const mapStateToProps = (state) => {
    return {
        network: state.appState.network,
    }
};

const mapDispatchToProps = (dispatch) => ({
    changeMainnet: bindActionCreators(mainnet, dispatch),
    changeTestnet: bindActionCreators(testnet, dispatch),
    changeDevnet: bindActionCreators(devnet, dispatch),
});


export default connect(mapStateToProps, mapDispatchToProps)(API);

export {
    URL, WS_URL, API, IP_ADDR
}
